<style>
  .scrollable {
    overflow: auto;
    font-size: 14px;
  }

  .detail-container {
    width: 800px;
  }

  .inner-line {
    margin-top: 10px;
  }

  .test-right {
    text-align: right;
    line-height: 26px;
  }

  .grey-title {
    height: 26px;
    line-height: 26px;
    background-color: #2d2c32;
    text-align: center;
    border: 1px solid #39383f;
  }

  .vertical-line {
    float: left;
    width: 26px;
    text-align: center;
    padding: 6px;
    border-right: 1px solid #39383f;
    border-bottom: 1px solid #39383f;
    display: flex;
    /*margin: auto 0;*/
  }

  .vertical-line.width {
    width: 54px;
  }

  .vertical-line.double {
    width: 79px;
  }

  .vertical-text {
    margin: auto;
  }

  .inner-row {
    margin-top: 5px;
  }

  .main-text {
    line-height: 26px;
  }

  .text-div {
    float: left;
    width: 648px;
    padding: 10px;
    border-right: 1px solid #39383f;
    border-bottom: 1px solid #39383f;
  }

  .checkbox-label {
    margin-left: 5px;
    font-size: 12px;
    color: #aaa;
  }

  .line-26 {
    line-height: 26px;
  }

  .next-line {
    padding-left: 50px;
  }
  .href-span {
    color: #ddd;
    cursor: pointer;
  }
  .href-span:hover {
    color: #a9b2fd;
  }
  .record-back-to-list {
    position: absolute;
    top: 3px;
    right: 3px;
    cursor: pointer;
    height: 33px;
    width: 72px;
    background: url('../../../assets/content/button_back_default.png') no-repeat;
    background-size: 100% 100%;
  }
  .record-back-to-list:hover {
    background: url('../../../assets/content/button_back_active.png') no-repeat;
    background-size: 100% 100%;
  }
</style>
<template>
  <!--维保工作->故障维修-->
  <div class="right-container scrollable">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
        <span class="page-title">检查记录</span>
      </iCol>
    </Row>
    <div class="record-back-to-list" @click="goBack"></div>
    <Row class="detail-container">
      <Row class="inner-line" :gutter="4">
        <iCol span="3" class="test-right">
          <span>单位：</span>
        </iCol>
        <iCol span="9">
          <Input class="grey" v-model="taskDetail.orgName" :disabled="disabled"/>
        </iCol>
        <iCol span="3" class="test-right">
          <span>建筑：</span>
        </iCol>
        <iCol span="9">
          <Input class="grey" v-model="taskDetail.buildingName" :disabled="disabled"/>
        </iCol>
      </Row>
      <Row class="inner-line" :gutter="4">
        <iCol span="3" class="test-right">
          <span>地址：</span>
        </iCol>
        <iCol span="21">
          <Input class="grey" v-model="taskDetail.buildingAddr" :disabled="disabled"/>
        </iCol>
      </Row>
      <Row class="inner-line" :gutter="4">
        <iCol span="3" class="test-right">
          <span>检查日期：</span>
        </iCol>
        <iCol span="5">
          <Input class="grey" v-if="taskDetail !== undefined && taskDetail.task !== undefined" :value="common.revertYTime(taskDetail.task.checkInDt)" :disabled="disabled"/>
        </iCol>
        <iCol span="3" class="test-right">
          <span>检查状态：</span>
        </iCol>
        <iCol span="5">
          <Input class="grey" v-model="taskDetail.task.status" :disabled="disabled"/>
        </iCol>
        <iCol span="3" class="test-right">
          <span>检查结果：</span>
        </iCol>
        <iCol span="5">
          <Input class="grey" v-model="taskDetail.task.result" :disabled="disabled"/>
        </iCol>
      </Row>
      <Row class="inner-line" :gutter="4">
        <iCol span="3" class="test-right">
          <span>检查人员：</span>
        </iCol>
        <iCol span="9">
          <Input class="grey" :value="taskDetail.primaryExecutorName" :disabled="disabled"/>
        </iCol>
        <iCol span="3" class="test-right">
          <span>单位协同人员</span>
        </iCol>
        <iCol span="9">
          <Input class="grey" :disabled="disabled"/>
        </iCol>
      </Row>
    </Row>
    <Row class="detail-container" style="margin-top: 30px;">
      <iCol span="3">&nbsp;</iCol>
      <iCol span="21" style="background-color: #202025; font-size: 12px;">
        <Row class="grey-title">
          <span>检查内容和情况记录</span>
        </Row>
        <Row v-if="taskDetail.task.type === 'SUPERVISE'" style="display: flex;">
          <iCol class="vertical-line width"
                style="background-color: #2d2c32; border: 1px solid #39383f; border-top: none;">
            <span class="vertical-text">单位履行消防安全职责情况</span>
          </iCol>
          <iCol style="float: left; width: 646px;">
            <!--小结开始-->
            <Row style="display: flex;" v-for="checkSection in checkList.checkSection"
                 v-if="checkSection.key !== 'CMWYH'">
              <iCol class="vertical-line">
                <span class="vertical-text">{{ checkSection.label }}</span>
              </iCol>
              <iCol class="text-div">
                <Row class="inner-row" v-for="item in checkSection.items">
                  <iCol span="24" class="item-label" style="z-index: 10;">
                    <span class="vertical-text">{{ item.order }}.{{ item.label }}</span>
                    <a @click="showImgListPreview(item.checkingPhotos)">
                      <Icon type="image" color="#4f83d2" size="18" class="cursor-pointer"
                            v-if="item.checkingPhotos !== undefined && item.checkingPhotos !== null && item.checkingPhotos.length > 0">
                      </Icon>
                    </a>
                  </iCol>
                  <Row class="next-line">
                    <iCol v-for="option in item.options"
                          :class="(option.optionType === 'I' ? ' ivu-col-span-24' : (option.label.length <= 4 ? ' ivu-col-span-4' : (option.label.length <= 9 ? ' ivu-col-span-8' : ' ivu-col-span-12')))">
                      <Checkbox size="large" v-if="option.optionType === 'C'" :ref="option.key"
                                :value="option.optionValue === 'Y' ? true : false" :disabled="disabled">
                        <span class="checkbox-label">{{ option.label}}</span>
                      </Checkbox>
                      <span v-else>
                      <span>{{ option.label}}</span>
                      <Input class="grey" :ref="option.key" style="width: 90%;" :value="option.optionValue" :disabled="disabled"/>
                    </span>
                    </iCol>
                  </Row>
                </Row>
              </iCol>
            </Row>
          </iCol>
        </Row>
        <Row v-else style="display: flex;">
          <iCol class="vertical-line double"
                style="background-color: #2d2c32; border: 1px solid #39383f; border-top: none;">
            <span class="vertical-text">其他检查情况</span>
          </iCol>
          <iCol class="text-div" style="float: left; width: 621px; border-bottom: 1px solid #39383f; border-right: 1px solid #39383f;">
            <!--小结开始-->
            <Row class="inner-row">
              <iCol span="24" class="item-label" style="z-index: 10;">
                <span class="vertical-text">{{ checkList.checkRemark }}</span>
                <a @click="showImgListPreview(checkList.correntPictures)">
                  <Icon type="image" color="#4f83d2" size="18" class="cursor-pointer"
                        v-if="checkList !== undefined && checkList.correntPictures !== undefined
                         && checkList.correntPictures !== null && checkList.correntPictures.length > 0">
                  </Icon>
                </a>
              </iCol>
            </Row>
          </iCol>
        </Row>

        <Row style="display: flex;" v-if="checkList !== undefined && checkList.checkSection !== undefined">
          <iCol class="vertical-line double" style="border-left: 1px solid #39383f;">
            <span class="vertical-text">{{ checkList.checkSection['CMWYH'].label }}</span>
          </iCol>
          <iCol class="text-div" style="width: 621px;">
            <Row class="inner-row" v-for="item in checkList.checkSection['CMWYH'].items">
              <iCol span="24" class="item-label" style="z-index: 10;">
                <span class="vertical-text">{{ item.order }}.{{ item.label }}</span>
                <a @click="showImgListPreview(item.checkingPhotos)">
                  <Icon type="image" color="#4f83d2" size="18" class="cursor-pointer"
                        v-if="item.checkingPhotos !== undefined && item.checkingPhotos !== null && item.checkingPhotos.length > 0">
                  </Icon>
                </a>
              </iCol>
              <Row class="next-line">
                <iCol v-for="option in item.options"
                      :class="(option.optionType === 'I' ? ' ivu-col-span-24' : (option.label.length <= 4 ? ' ivu-col-span-4' : (option.label.length <= 9 ? ' ivu-col-span-8' : ' ivu-col-span-12')))">
                  <Checkbox size="large" v-if="option.optionType === 'C'" :ref="option.key"
                            :value="option.optionValue === 'Y' ? true : false" :disabled="disabled">
                    <span class="checkbox-label">{{ option.label}}</span>
                  </Checkbox>
                  <span v-else>
                      <span>{{ option.label}}</span>
                      <Input class="grey" :ref="option.key" style="width: 90%;" :value="option.optionValue"/>
                    </span>
                </iCol>
              </Row>
            </Row>
          </iCol>
        </Row>
        <Row style="display: flex; min-height: 50px;">
          <iCol class="vertical-line double" style="border-left: 1px solid #39383f;">
            <span class="vertical-text">责令整改情况</span>
          </iCol>
          <iCol class="text-div" style="width: 621px;">
            <Row class="inner-row" v-for="(task, index) in checkList.correctionTasks">
              <span class="href-span" @click="showCorrectionDetail(task.taskId)">
                <span style="font-weight:bold;">{{ index+1 + '. ' + task.taskName}}</span>
                <span v-if="task.taskStatus === 'OPEN'">(未执行)</span>
                <span v-else-if="task.taskStatus === 'EXECUTING'">(执行中)</span>
                <span v-else>(已完成)</span>
                <span v-if="task.createrName !== undefined && task.createrName !== null && task.createrName.length !== 0"> - {{ task.createrName }}</span>
                <span v-if="task.showTime !== null"> - {{common.revertMTime(task.showTime)}}</span>
              </span>
              <span style="margin-left: 5px; color: #a9b2fd; cursor: pointer;" @click="showCorrectionDetail(task.taskId)">查看</span>
            </Row>
          </iCol>
        </Row>
        <Row style="display: flex; min-height: 50px;">
          <iCol class="vertical-line double" style="border-left: 1px solid #39383f;">
            <span class="vertical-text">备注</span>
          </iCol>
          <iCol class="text-div" style="width: 621px;">
            <Row class="inner-row">
            </Row>
          </iCol>
        </Row>
      </iCol>
    </Row>
    <ImagePreviewModal :fileList="fileList" :imagePreviewModal="imagePreviewModal" v-on:closeImagePreviewModal="closeImagePreviewModal"></ImagePreviewModal>
  </div>
</template>
<script>
  import Vue from 'vue';
  import server from "../../../config/api";
  import common from '../../../libs/common';
  import niceScroll from 'jquery.nicescroll';
  import ImagePreviewModal from '../../../components/image-preview.vue';

  export default {
    components: {
      'ImagePreviewModal': ImagePreviewModal
    },
    data() {
      return {
        taskId: undefined,
        imagePreviewModal: false,
        fileList: [],
        taskDetail: {
          task: {}
        },
        checkList: {},
        common: common,
        disabled: true,
      };
    },
    methods: {
      showCorrectionDetail: function(taskId) {
        this.$router.push({name: 'orderRectDetail', query: {taskId: taskId}});
      },
      closeImagePreviewModal: function() {
        this.imagePreviewModal = false;
      },
      showImgListPreview: function(imgList) {
        this.fileList = imgList;
        this.imagePreviewModal = true;
      },
    },
    mounted() {
      this.taskId = this.$route.query.taskId;
      this.$http.get(server.api.routine.record.detail.replace('{taskId}', this.taskId)).then((response) => {
        let checkList = JSON.parse(response.body.data.task.taskContent);
//        this.checkList = JSON.parse(response.body.data.task.taskContent);
        response.body.data.task.taskContent = undefined;
        this.taskDetail = response.body.data;
        let status = this.taskDetail.task.status;
        if(status === 'OPEN') {
          status = '未执行';
        } else if(status === 'EXECUTING') {
          status = '执行中';
        } else if(status === 'DONE') {
          status = '已完成';
        }
        this.taskDetail.task.status = status;

        let result = this.taskDetail.task.result;
        if(result === 'FAIL') {
          result = '不合格';
        } else if(result === 'PASS') {
          result = '合格';
        } else {
          result = '未确认';
        }
        this.taskDetail.task.result = result;

        for (let index in checkList.checkSection) {
          let section = checkList.checkSection[index];
          section.items = _.sortBy(section.items, 'order');
          for (let jndex in section.items) {
            let item = section.items[jndex];
            item.options = _.sortBy(item.options, 'order');
          }
        }
        this.checkList = checkList;
        Vue.nextTick(() => {
          $(".right-container").niceScroll({
            cursorcolor: "rgb(99, 114, 131)",
            cursorwidth: "7px",
            cursoropacitymax: 0.4,
            cursorborder: "", // css definition for cursor border
            cursorborderradius: "",
            hidecursordelay: 1000,
          });
        });
      }, (error) => {
        this.$Message.error("获取记录失败！");
      });
    }

  }
</script>



